import Vue from 'vue'
import App from './App'
import router from './router'
import AOS from 'aos'
import 'aos/dist/aos.css'
import Sticky from 'sticky-js'
import vueScrollto from 'vue-scrollto'
Vue.config.productionTip = false
Vue.use(vueScrollto)
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})
Vue.dire



